.page-footer {
  background-color: $grey;
  padding-top: 36px;
  padding-bottom: 48px;
  position: relative;
}

.page-footer__logo {
  display: block;
  margin-bottom: 30px;
  text-align: center;
}

.page-footer__socials {
  margin-top: 30px;

  @media (min-width: $tablet-width) {
    margin-top: 70px;
  }
}

.page-footer__nav-list {
  @include list-none;
  margin: 0 auto;
  margin-bottom: 30px;
}